.scale{
  padding: 9.375em 0 3em;
  .line-title{
    @media screen and (min-width: 993px){
      &__item{
        &:nth-child(2){
          padding-left: 9.25em;
        }
      }
    }
  }
  &__inner{
    margin-top: 10em;
    position: relative;
  }
  &__planet{
    width: 62.625em;
    position: relative;
    &-img{
      width: 100%;
      display: block;
    }
    &-star{
      position: absolute;
      width: 17.375em;
      left: 23.75em;
      top: -4.75em;
    }
  }
  &__item{
    position: absolute;
    color: $secondary;
    .h3{
      text-transform: unset;
      font-weight: 400;
    }
    img{
      position: absolute;
      width: 17.375em;
      right: 100%;
      top: 0;
      opacity: 0;
      display: none;
    }
    &._show{
      color: #fff;
      img{
        opacity: 1;
      }
    }
  }
  &__item1{
    top: -1em;
    left: 37.5em;
    img{
      transform: translate(4em,-2.5em);
    }
  }
  &__item2{
    top: 11em;
    left: 53.375em;
    img{
      transform: translate(4.25em,-3.5em);
    }
  }
  &__item3{
    top: 23em;
    left: 63em;
    img{
      transform: translate(3.5em,-3.5em);
    }
  }
  &__item4{
    top: 35em;
    left: 67.5em;
    img{
      transform: translate(2.75em,-4em);
    }
  }
  &__item5{
    top: 47em;
    left: 70.25em;
    img{
      transform: translate(0,-6em);
    }
  }
}

.your-way{
  padding: 9.375em 0;
  overflow: hidden;
  .line-title{
    @media screen and (min-width: 993px){
      &__item{
        &:nth-child(2){
          padding-left: 6.75em;
        }
      }
    }
  }
  &__items{
    margin-top: 12.5em;
    width: 50.25em;
    max-width: 100%;
    display: flex;
  }
}

.service-card{
  width: 100%;
  flex-shrink: 0;
  padding-right: 14em;
  &__img{
    width: 28.25em;
    height: 23em;
    max-width: 100%;
    display: flex;
    align-items: center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__text{
    @include grid100gap(1.875em);
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .scale{
    padding: 3.75em 0;
    @media screen and (min-width: 577px){
      &__inner{
        font-size: .4em;
      }
    }
  }

  .your-way{
    padding: 3.75em 0;
    &__items{
      @include grid100gap(4.375em);
      margin-top: 5em;
      width: 100%;
      @media screen and (min-width: 577px){
        grid-template-columns: repeat(2,calc(50% - 2.1875em));
        .service-card__img{
          height: 11em;
        }
      }
    }
  }

  .service-card{
    padding: 0;
    &__img{
      width: auto;
      max-width: 12.5em;
      height: auto;
      max-height: 11em;
      margin: 0 auto;
    }
    &__text{
      gap: 1.25em;
      margin-top: 3.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .scale{
    &__planet{
      width: 100%;
      &-img{
        transform: rotate(25deg);
        margin-left: -10.5em;
      }
      &-star{
        width: 6em;
        left: -1.25em;
        top: -2.875em;
      }
    }
    &__inner{
      margin-top: 5em;
    }
    &__item{
      .h3{
        font-size: 1.125em;
      }
      img{
        width: 6em;
        transform: translate(0em,-1.5em);
      }
    }
    &__item1{
      top: -.5em;
      left: 5.625em;
    }
    &__item2{
      top: 3.625em;
      left: 9.375em;
    }
    &__item3{
      top: 7.5em;
      left: 11.125em;
    }
    &__item4{
      top: 12.625em;
      left: 11.5em;
    }
    &__item5{
      top: 18.5em;
      left: 10.5em;
    }
  }
}
