.equip{
  padding: 9.375em 0;
  .line-title{
    @media screen and (min-width: 993px){
      &__item{
        &:nth-child(2){
          padding: 0;
          text-align: right;
        }
      }
    }
  }
  &__content{
    height: 60em;
    margin-top: 7em;
    position: relative;
  }
  &__star{
    position: absolute;
  }
  &__star1{
    width: 22.375em;
    left: 50%;
    bottom: 16.5em;
    transform: translateX(-50%) rotate(30deg);
  }
  &__star2{
    top: 0;
    left: 26.25em;
    transform: rotate(-174deg);
    width: 5.375em;
  }
  &__star3{
    transform: rotate(2deg);
    right: 2.875em;
    top: 10.25em;
    width: 7.875em;
  }
  &__star4{
    transform: rotate(174deg);
    right: 13.75em;
    bottom: -1.5em;
    width: 5.125em;
  }
  &__star5{
    transform: rotate(-159deg);
    left: 3.875em;
    bottom: 11.25em;
    width: 6.375em;
  }
  &__item{
    position: absolute;
    @media screen and (min-width: 993px){
      &:not(:hover){
        path{
          fill: $secondary;
        }
      }
    }
    svg{
      width: 100%;
    }
  }
  &__item1{
    top: 5.5em;
    left: 38.125em;
    width: 13.125em;
  }
  &__item2{
    top: 13.125em;
    left: 19.5em;
    width: 7.5em;
  }
  &__item3{
    top: 13.125em;
    left: 60.375em;
    width: 13.25em;
  }
  &__item4{
    top: 20.5em;
    left: 32.625em;
    width: 9.875em;
  }
  &__item5{
    top: 19.625em;
    left: 79.375em;
    width: 7em;
  }
  &__item6{
    top: 25em;
    left: 8.75em;
    width: 10.8125em;
  }
  &__item7{
    top: 29.1875em;
    left: 87.1875em;
    width: 9.6875em;
  }
  &__item8{
    top: 33.9375em;
    left: 30.1875em;
    width: 5.5em;
  }
  &__item9{
    top: 32.5em;
    left: 69.5em;
    width: 5.875em;
  }
  &__item10{
    top: 43.3125em;
    left: 17em;
    width: 8.125em;
  }
  &__item11{
    top: 44.5em;
    left: 51.25em;
    width: 9.375em;
  }
  &__item12{
    top: 40.5em;
    left: 85.25em;
    width: 4.875em;
  }
  &__item13{
    top: 51.5em;
    left: 36.8125em;
    width: 5.25em;
  }
  &__item14{
    top: 49.7em;
    left: 73.375em;
    width: 7.75em;
  }
}


@media screen and (max-width: 992px){
  .equip{
    padding: 3.75em 0;
    &__content{
      font-size: .5em;
      margin-left: -8.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .equip{
    &__content{
      font-size: 1em;
      height: 39.375em;
