.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .2;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: $bg;
        width: 39em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 3.75em 3.125em;
        border-radius: 1.5em;
        display: inline-block;
        box-shadow: 0 .25em .25em 1em rgba($primary, 0.04),
                    0 .625em 1.75em 1em rgba($primary, 0.25);
    }
    &__text{
        max-width: 20em;
        @include grid100gap(1.375em);
    }
    .close-btn{
        position: absolute;
        top: 1.875em;
        right: 1.875em;
    }
}

.close-btn{
    background-color: transparent;
    flex-shrink: 0;
    width: 1.8125em;
    @include center;
    &:hover{
        path{
            fill: $primary;
        }
    }
    svg{
        width: 100%;
    }
}

.thank-modal{
    .modal__dialog{
        padding-bottom: 13em;
        background-image: url(../img/modal-bg.webp);
        background-size: 20.9375em auto;
        background-position: right bottom;
        background-repeat: no-repeat;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
    }
}

//transform: translate(166%, 269%);
@media screen and (max-width: 576px){
    .modal{
        &:after{
            display: none;
        }
        &__dialog {
            overflow-y: auto;
            top: 0;
            left: 0;
            position: fixed;
            width: 100%;
            max-width: unset;
            height: $vh;
            border-radius: 0;
            padding: 7em 1.625em 2em;
        }
        &__text{
            gap: 1.25em;
        }
        &__desc{
            font-size: 1.125em;
            br{
                display: none;
            }
        }
    }
    .thank-modal{
        .modal__dialog{
            padding-bottom: 30em;
            background-image: url(../img/modal-bg2.webp);
            background-size: 17.5em auto;
        }
    }
}

