.main{
  min-height: $vh;
  padding: 21.875em 0;
  text-align: center;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 18.75em;
    background: linear-gradient(180.00deg, rgb(0, 0, 0),rgba(0, 0, 0, 0) 100%);
    opacity: 0.5;
  }
  .container{
    position: relative;
    z-index: 3;
  }
  .h1,
  &__text{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &._show{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__text{
    margin: 3.125em auto 0;
    width: 50em;
    max-width: 100%;
    .h3{
      font-weight: 400;
      text-transform: unset;
    }
    br{
      display: none;
    }
  }
  &__video{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 2;
  }
}

@keyframes rotation {
  0% {
    transform:rotate(0deg);
  }
  100% {
    transform:rotate(360deg);
  }
}

.loader{
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $vh;
  background-color: $bg;
  @include center;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &._show{
    opacity: 1;
    pointer-events: unset;
  }
  &__img{
    width: 6.0625em;
    animation-name: rotation;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }
}

._loading{
  overflow-y: hidden;
  //padding-right: var(--scrollWidth);
}


@media screen and (max-width: 992px){
  .main{
    padding: 0;
    &:before{
      display: none;
    }
    &__inner{
      height: $vh;
      @include center;
      flex-direction: column;
      width: 15em;
      max-width: 100%;
      margin: 0 auto;
    }
    .h3{
      font-size: 1.125em;
    }
    &__text{
      margin-top: .75em;
      br{
        display: unset;
      }
    }
  }
}


