.feedback{
  padding: 9.375em 0 12.5em;
  background-image: url(../img/faq-bg.webp);
  background-size: 87.5em auto;
  background-repeat: no-repeat;
  background-position: center bottom 0;
  position: relative;
  &:after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 23em;
    background: linear-gradient(0deg, #000 0%, #000 0%, rgba(0, 0, 0, 0.9) 26.93%, rgba(0, 0, 0, 0) 100%);
  }
  &__grid{
    position: relative;
    z-index: 2;
    margin-top: 4.375em;
    display: grid;
    grid-template-columns: 39em 37.5em;
    justify-content: space-between;
    align-items: flex-end;
    gap: 6.25em 0;
  }
}

.feedback-form{
  @include grid100gap(.625em);
  &__btn{
    margin-top: 3.125em;
    display: inline-flex;
    margin-right: auto;
    svg{
      width: 1.875em;
      margin-left: 5.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .feedback{
    padding: 3.75em 0 6.875em;
    background-size: 100% auto;
    &:after{
      height: 18em;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 6em;
    }
    &__col2{
      order: -1;
    }
  }
}


@media screen and (max-width: 576px){
  .feedback{
    background-image: url(../img/faq-bg2.webp);
    background-position: left 0 bottom 6.875em;
    &:after{
      display: none;
    }
  }

  .feedback-form{
    gap: .3125em;
    &__btn{
      width: 100%;
      justify-content: space-between;
      svg{
        width: 1.3125em;
        margin: 0;
      }
    }
  }
}
