.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  padding: 1.875em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 12.125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone{
    font-weight: 100;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0;
    &__logo{
      width: 8em;
    }
    &__phone{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__logo{
      width: 6.125em;
    }
    &__phone{
      font-size: .875em;
    }
  }
}
