@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 4em;
    border-radius: 6.25em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.5em;
    }
}

.m-btn-white{
    background-color: #fff;
    color: $black;
    &:hover{
        background-color: $primary;
        color: $black;
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        height: 3.25em;
        span{
            font-size: 1.125em;
        }
    }
}
