.set-equip{
  padding: 9.375em 0 6.25em;
  &__inner{
    padding: 12.5em 0 0 58.125em;
    position: relative;
  }
  &__groups{
    @include grid100gap(6.25em);
    position: relative;
    z-index: 2;
  }
  &__group{
    .m-list{
      margin-top: 1.875em;
    }
  }
  &__planet{
    position: absolute;
    img{
      width: 100%;
      display: block;
    }
  }
  &__planet1{
    width: 27.5625em;
    top: 8.625em;
    left: 19.375em;
    opacity: .4;
  }
  &__planet2{
    width: 19.9375em;
    opacity: .4;
    left: 2.5em;
    top: 25.3125em;
    z-index: 2;
  }
  &__planet3{
    bottom: -3.625em;
    left: 4.125em;
    width: 58.5625em;
  }
}

.m-list{
  padding-left: .125em;
  li{
    position: relative;
    padding-left: 1em;
    font-size: 1.75em;
    line-height: 125%;
    letter-spacing: -0.02em;
    &:before{
      content: '•';
      position: absolute;
      top: 0;
      left: 0;
      background-image: unset;
      width: unset;
      height: unset;
    }
  }
}

.advantages{
  padding: 6.25em 0 9.375em;
  &__items{
    margin-top: 10em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 16.25em / 3));
    gap: 8.125em;
  }
}

.line-title{
  @media screen and (min-width: 993px){
    @include grid100gap(1.5625em);
    &__item{
      &:nth-child(2){
        padding-left: 5.375em;
      }
      &:nth-child(3){
        text-align: right;
      }
    }
  }
}

.advantage-card{
  &__img{
    height: 6em;
    display: flex;
    flex-grow: 1;
    margin-bottom: 1.625em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .set-equip{
    padding: 7.5em 0 3.125em;
    overflow: hidden;
    &__inner{
      padding: 3.75em 0 0;
    }
    &__groups{
      gap: 2.5em;
    }
    &__group{
      .m-list{
        margin-top: 1.25em;
      }
    }
    &__planet1{
      top: 2.5em;
      right: -.3125em;
      left: unset;
      width: 13em;
    }
    &__planet2{
      top: 13.375em;
      right: -.625em;
      left: unset;
      width: 8.375em;
    }
    &__planet3{
      width: 25.625em;
      bottom: -1.375em;
      left: unset;
      opacity: .4;
      right: -6.5em;
    }
  }

  .m-list{
    li{
      font-size: 1.125em;
    }
  }

  .advantages{
    padding: 3.125em 0 3.75em;
    &__items{
      margin-top: 3.75em;
      grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
      gap: 2.5em;
    }
  }

  .advantage-card{
    &__img{
      height: 3.75em;
      margin-bottom: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
