.footer{
  padding-bottom: 6.25em;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    width: 16.125em;
    display: block;
    margin-bottom: 2em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__col2{
    text-align: right;
    @media screen and (min-width: 577px){
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-end;
      .footer__contacts{
        margin-top: 1.33em;
      }
    }
  }
  &__contacts{
    margin-top: 2.67em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-bottom: 1.875em;
    &__logo{
      width: 10em;
      margin-bottom: 1.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      display: block;
    }
    &__logo{
      width: 8.25em;
    }
    &__col2{
      margin-top: 1.25em;
      display: flex;
      flex-direction: column;
      text-align: left;
    }
    &__contacts,
    &__email,
    &__address{
      font-weight: 100;
      font-size: .875em;
    }
    &__contacts{
      order: -1;
      margin: 0 0 .7em;
    }
  }
}
